.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10; }
  .popup .popup_inner {
    position: absolute;
    width: 705px;
    left: 25%;
    right: 20%;
    top: 7%;
    margin: auto;
    background: white;
    display: grid;
    grid-template-rows: 70px 40px 50px 30px 30px;
    border-top: 4px solid white;
    border-left: 4px solid white;
    border-right: 4px solid white;
    border-radius: 10px;
    z-index: 15; }
    .popup .popup_inner h2 {
      padding-left: 22px;
      font-size: 20px;
      font-family: 'hk_novaextrabold';
      color: #28394f;
      grid-row: 1;
      margin-top: 20px; }
    .popup .popup_inner .close {
      width: 20%;
      height: 20px;
      grid-row: 1;
      grid-column: 4;
      justify-self: end;
      margin: 2.2em; }
    .popup .popup_inner img {
      cursor: pointer;
      color: #28394f; }
    .popup .popup_inner button {
      justify-self: end;
      margin-top: 1.6em;
      justify-self: center;
      grid-row-start: 5;
      grid-row-end: 7;
      grid-column: 4; }
    .popup .popup_inner label {
      font-size: 1.4em;
      font-family: 'hk_novamedium';
      margin-left: 40px; }
    .popup .popup_inner .probleem {
      grid-row: 2;
      margin-left: 40px; }
    .popup .popup_inner textarea {
      grid-row: 3;
      border: 1px solid #a8a8a8;
      border-radius: 10px;
      padding: 10px;
      font-size: 1em;
      margin-left: 40px;
      -ms-flex-item-align: start;
          align-self: start; }
    .popup .popup_inner .defect {
      grid-row: 5;
      margin-left: 40px; }
    .popup .popup_inner section {
      grid-row: 6;
      margin-bottom: 20px;
      margin-left: 40px; }



