html,
body {
  margin: 0;
  padding: 0;
  font-family: 'hk_novaregular', sans-serif;
  font-size: 10px; }

body {
  min-width: 100vw;
  min-height: 100vh;
  font-size: 1.4rem;
  text-rendering: geometricPrecision; }

div {
  position: static; }

[class^='icon-']:before,
[class*=' icon-']:before,
[class^='icon-']:after,
[class*=' icon-']:after {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-bread:before {
  content: '\e900';
  font-weight: 600;
  margin-top: 4px; }

.icon-deal:before {
  content: '\e901';
  font-weight: 600;
  margin-top: 4px; }

.icon-search:before {
  content: '\e903';
  font-weight: 600;
  margin-top: 4px; }

.icon-location2:before {
  content: '\e948';
  font-weight: 600;
  margin-top: 4px; }

.intro {
  position: absolute;
  top: 320px;
  left: 650px; }

.loading {
  max-width: 10%;
  display: block; }

.spinner {
  width: 4%;
  float: left;
  margin-right: 10px;
  margin-top: 5px; }



