.blue {
  background-color: #28394f;
  padding-bottom: 20px; }
  .blue input {
    border: 0;
    width: 30px;
    height: 15px;
    border-radius: 8px;
    margin: 10px 0 0 9px;
    padding: 10px;
    font-size: 1em;
    -webkit-transition: width 1s;
    -o-transition: width 1s;
    transition: width 1s; }
  .blue input:hover {
    width: 268px; }



