.upperbar {
  grid-row: 1;
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
  grid-template-columns: 1fr 280px;
  height: 80px;
  border-bottom: 1px solid #a8a8a8; }
  .upperbar img {
    padding: 0px;
    width: 10%;
    margin-top: 4px;
    margin-left: 20px; }
  .upperbar h6 {
    margin: 0px;
    font-size: 1em;
    margin-top: 35px;
    color: #28394f; }



